    margin: 0px;
    gap: 0;
    top: 0;
    padding-top: 14vw;
  }
  .activeNav{
    display: flex;
  }
  header nav a{
    width: 90%;
    background-color: transparent;
    border-top: .1px solid var(--bg-darker);
    border-bottom: .1px solid var(--bg-darker);
  }
  header nav a:hover{
    animation: none;
  }
